.Login{
    width: 100%;
    height: 98vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
}
.Login-Card{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.Login-Inputs{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.Login-input{
    margin: 5px 0;
    width: 50%;
}

.Login-Button{
    margin-top: 5px;
    width: 50.7%;
    text-align: center;
    text-decoration: none;
    padding: 10px 0;
    cursor: pointer;
    background-color: #3b3b3b;
    color: white;
}
