.ChatRoom {
  width: 600px;
  height: max-content;
  box-shadow: 1px 1px 10px 10px #ccc;
  margin: 10px;
  display: flex;
  flex-direction: column;
}

.header {
  height: max-content;
}

.main {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.body {
  background-color: #3b3b3b;
  flex: 1;
  margin-bottom: 3px;
}


.chat-main {
  height: 700px;
  overflow: auto;
  padding: 20px 10px;
}

.messages {
    text-align: left;
  color: white;
  font-size: 20px;
}

.chat-card{
    margin: 15px 0;
}

.chat-card{
    width: 50%;
    padding: 10px 10px;
    background-color: #ddcaca;
    border-radius: 10px 10px 10px 0;
    text-align: left;
}

.card-body{
    margin-top: 5px;
    padding: 10px 10px;
}

.footer {
  height: 40px;
}
.input {
  width: 85%;
  border-radius: 0;
  outline: none;
  border: 1px solid #ccc;
  height: 30px;
  padding: 0 5px;
  transition: 0.2s;
}
.input:focus {
  border: 1px solid #868484;
}
.button {
  width: 10%;
  height: 32px;
  border-radius: 0;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: #3b3b3b;
  color: white;
}
.ul {
  padding: 0;
  list-style: none;
  display: flex;
  margin: 0;
}
.li-active{
  background-color: #ccc;
}
.ul > li {
  padding: 15px 15px;
  transition: 0.3s;
}
.ul > li:hover {
  cursor: pointer;
  background-color: #ccc;
}
